@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500&display=swap');

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-header {
  background-color: rgb(255, 255, 255);
  max-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'Space Grotesk';
  font-size: 18px;
  color: rgb(0, 0, 0);
}

.card{
  width: 40vw;
  height: 100vh;
  background-image:url(./images/bg-main-desktop.png); 
  background-size: cover;
  position: relative;
}

.data{
  display:grid;
  height: 100vh;
  place-items:center;
  margin-left: 240px;
  text-align: left;
  letter-spacing: 1px;
  position: relative;
}

/* .finalImage{
  background-image:url("images/icon-complete.svg");
  background-repeat: no-repeat;
  background-size: cover; 
} */

.error{
  color: hsl(0, 100%, 66%);
  position: relative;
  bottom: 30px;
  margin-right:10px;
  font-size: 12px;
}

.dataInput{
  cursor: pointer;
  width: 370px;
  height: 50px;
  padding-left: 15px;
  border-radius: 10px;
  border-color: rgb(222, 221, 223);
  border-width: 1px;
  margin-top: 13px;
  margin-bottom: 30px;
}

.data input:focus{
  outline: none;
  border-color:rgb(96, 5, 148);
}

.dataExpires{
  display: flex;
  flex-direction: row;
  position: relative;
}

.dataExpires input{
  width: 100px;
  margin-top: 10px;
}

.left{
  position: absolute;
  left: 110px;
}

.leftCvc{
  position: absolute;
  left: 220px;
}

.leftCvc input{
  width: 150px;
}

.invalid{
  cursor: pointer;
  width: 370px;
  height: 50px;
  padding-left: 15px;
  border-radius: 10px;
  border-width: 1px;
  margin-top: 13px;
  margin-bottom: 30px;
  border-color:hsl(0, 100%, 66%); 
  
}

.btn{
  width: 370px;
  height: 50px;
  color:rgb(255, 255, 255); 
  background-color: rgb(33, 9, 47);   
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-size: 18px;
  letter-spacing: 1px;
}

.frontCard{
  width: 33%;
  height: 33%;
  background-image: url(./images/bg-card-front.png);
  background-size: cover;
  position: absolute;
  top: 70px;
  left: 145px;
  text-align: left;
  border-radius: 10px;
  font-family: 'Space Grotesk';
  color:rgb(255, 255, 255);
  letter-spacing: 1px;
  padding: 25px;
  
}

.frontCard h2{
  letter-spacing: 2px;
  font-size: 145%;
  margin-top: 20%;
  margin-bottom: 1%;
  margin-left: 5%;
}

.final{
  visibility: hidden;
  position: absolute;
  top: 200px;
  right: 150px;
}

.final h1{
  margin: 20px 0px;
  letter-spacing: 2px;
}

.final h3{
  margin-bottom: 20px;
  color: hsl(279, 6%, 55%)
}
.example{
  display: flex;
  flex-direction: row;
  margin-left: 5%;
}

.month{
  position: absolute;
  right: 10%;
}

.year{
  position:absolute;
  right: 4%;
}

.backCard{
  width: 33%;
  height: 33%;
  background-image: url(./images/bg-card-back.png);
  background-size: cover;
  position: absolute;
  top: 370px;
  left: 240px;
  text-align: right;
  border-radius: 10px;
  font-family: 'Space Grotesk';
  color:rgb(255, 252, 252);
  letter-spacing: 1px;
  
}

.backCard p{
  position: relative;
  top: 110px;
  right: 10%;
}

@media(max-width: 650px){
  .App-header{
    display: flex;
    flex-direction: column;
    
  }
  .card{
    width: 100vw;
    height: 300px;      
  }
  .frontCard{
    width: 400px;
    height: 220px;
    position: absolute;
    left:15px;
    top: 160px;
    z-index: 3;
    
  }
  .frontCard h2{
    letter-spacing: 2px;
    margin-top: 45px;
    margin-bottom: 10px;
  }
  .month{
    position: absolute;
    right: 35px;
  }
  .year{
    position:absolute;
    right: 10px;
  }
  .backCard{
    width: 400px;
    height: 220px;
    position: absolute;
    left: 75px;
    top: 20px;
    z-index: 1;
  }
  .backCard p{
    position: absolute;
    top:100px;
    right: 40px;
  }
  .data{
    width: 100vw;
    margin-top:10px;
    margin-left: 15px;
    position: absolute;
    left: -20px;
    text-align: left;
    letter-spacing: 1px;
  }
  .data input{
    width:450px
  }
  .dataExpires input{
    width: 100px;
    margin-right: 20px;
  }
  .leftCvc input{
    width: 225px;
  }
  .final{
    top:400px;
    right: 10px;
  }
  .final btn{
    width: 300px; 
  }
  .btn{
    width: 455px;

  }
}
